import React, { Component } from "react";
import {
  SkillContainer,
  SkillH2,
  SkillCardWrapper,
  SkillCard,
  // SkillCardContent,
} from "./SkillElements";

// import { CircleProgress } from "react-gradient-progress";
// import "aos/dist/aos.css";

import Css from "assets/images/css.svg";
import Html from "assets/images/html.svg";
import Js from "assets/images/javascript.svg";
import ReactIMG from "assets/images/react.svg";
import XD from "assets/images/adobe-xd.svg";
import Figma from "assets/images/figma.svg";

import { SkillSection as SkillAnimation } from "Animation";

class SkillSection extends Component {
  state = {
    skillcard: [
      {
        id: 1,
        img: <img src={Html} alt="HTML" />,
        // text: "lorem lorem lorem lorem lorem",
        // progress: 90,
        // color: ["#FC8C48", "#FABF9B"],
      },
      {
        id: 2,
        img: <img src={Css} alt="CSS" />,
        // text: "lorem lorem lorem lorem lorem",
        // progress: 90,
        // color: ["#70D6F9", "#1AA6D6"],
      },
      {
        id: 3,
        img: <img src={Js} alt="JavaScript" />,
        // text: "lorem lorem lorem lorem lorem",
        // progress: 70,
        // color: ["#FC657E", "#fabf9b"],
      },
      {
        id: 4,
        img: <img src={ReactIMG} alt="React" />,
      },
      {
        id: 5,
        img: <img src={Figma} alt="Figma" />,
      },
      {
        id: 6,
        img: <img src={XD} alt="Adobe XD" />,
      },
    ],
  };

  componentDidMount() {
    SkillAnimation(document.getElementById("skillTitle"));
  }
  // componentDidMount() {
  //   Aos.init({
  //     duration: 1500,
  //     once: false,
  //     offset: 2500,
  //     disable: function () {
  //       var maxWidth = 768;
  //       return window.innerWidth < maxWidth;
  //     },
  //   });
  // }

  // componentDidUpdate() {
  //   Aos.init({
  //     duration: 1500,
  //     once: false,
  //     disable: function () {
  //       var maxWidth = 768;
  //       return window.innerWidth < maxWidth;
  //     },
  //   });
  // }

  render() {
    // useEffect(() => {
    //   Aos.init({ duration: 1500 });
    // }, []);
    return (
      <>
        <SkillContainer id="skill">
          <SkillH2 id="skillTitle">My Skills</SkillH2>
          <SkillCardWrapper id="cardSkill">
            {this.state.skillcard.map((skill) => (
              <SkillCard key={skill.id}>
                {" "}
                {skill.img}
                {/* <SkillCardContent>
                  <CircleProgress
                    percentage={skill.progress}
                    strokeWidth={8}
                    width={150}
                    primaryColor={skill.color}
                  />
                </SkillCardContent> */}
              </SkillCard>
            ))}
          </SkillCardWrapper>
        </SkillContainer>
      </>
    );
  }
}

export default SkillSection;
