import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

export const ServiceSection = (service) => {
  let tl = gsap.timeline({
    scrollTrigger: {
      trigger: service,
      start: "top 50%",
      // markers: false,
    },
  });

  tl.from(
    "#serviceTitle",
    {
      ease: "power3.InOut",
      y: "30",
      delay: 0.4,
      opacity: 0,
    },
    0.2
  );
  tl.from(
    "#serviceP",
    {
      ease: "power3.InOut",
      y: "30",
      delay: 0.4,
      opacity: 0,
    },
    0.6
  );
  tl.from(
    "#serviceCard",
    {
      ease: "power3.InOut",
      y: "30",
      delay: 0.4,
      opacity: 0,
    },
    0.9
  );
};

