import styled from "styled-components";

export const SkillContainer = styled.div`
  width: 1000px;
  margin: 0 auto;
  padding: 80px 0;
  height: 60vh;

  @media screen and (max-width: 1200px) {
    width: 95%;
  }

  @media screen and (max-width: 768px) {
    height: 80vh;
  }
`;

export const SkillH2 = styled.h2`
  font-family: "Russo One", sans-serif;
  font-size: 36px;
  text-align: center;
  margin: 50px 0 50px 0;

  @media screen and (max-width: 768px) {
    font-size: 26px;
  }
`;

export const SkillCardWrapper = styled.div`
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-wrap: wrap;
  /* margin: 0 10px; */
`;

export const SkillCard = styled.div`
  /* flex-direction: column; */
  justify-content: center;
  /* box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); */
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  margin: 0 20px;
  padding: 20px 20px;
  border-radius: 9px;
  /* width: 200px; */
  /* width: 900px; */

  img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
  }

  :hover {
    /* -webkit-box-shadow: 13px 13px 7px -7px rgba(0, 0, 0, 0.17);
    -moz-box-shadow: 13px 13px 7px -7px rgba(0, 0, 0, 0.17);
    box-shadow: 5px 10px 40px #b3b3b3; */
    /* box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
      0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
      0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
      0 100px 80px rgba(0, 0, 0, 0.12);
    transition: 0.4s; */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    transform: scale(1.1);
    cursor: pointer;
  }

  @media screen and (max-width: 768px) {
    margin: 5px 10px;
    img {
      display: flex;
      /* justify-content: center;
      align-items: center; */
      width: 50px;
      height: 50px;
    }
  }
`;

export const SkillCardContent = styled.div``;

